<template>
  <div class="position-relative vw-100 vh-100 p-0 m-0">
    <canvas ref="test" class="position-absolute canvas-img" />
    <canvas class="position-absolute dummy canvas-img" ref="dummy" />
  </div>
</template>
<script>
import ImageCanvas from "../assets/dp.jpeg";
export default {
  mounted() {
    this.startPrepartion();
  },
  data() {
    return {
      isHidden: true,
      worker: null,
      Ccanvas: null,
      canvas: null,
      image: null,
    };
  },
  methods: {
    startPrepartion() {
      this.worker = new Worker("../service/imagePos.js", { type: "module" });
      this.$refs.test.height = this.$refs.dummy.height = screen.availHeight/1.5;
      this.$refs.test.width = this.$refs.dummy.width = screen.availWidth/2;
      this.canvas = this.$refs.test.transferControlToOffscreen();
      let events = [
        "mousemove",
        "moseover",
        "touchmove",
        "touchstart",
        "mouseleave",
        "touchend",
      ];
      events.forEach((event) => {
        this.$refs.test.addEventListener(event, this.debounce((e) => {
          let xPos = e.pageX;
          let yPos = e.pageY;
          this.worker.postMessage({
            isInitial: false,
            type: e.type,
            mouse: {
              x: xPos,
              y: yPos,
            },
          });
        }),1000);
      });
      this.init();
    },
    init() {
      this.image = new Image();
      this.image.src = ImageCanvas;
      let imageData = [];
      this.image.onload = () => {
        let canvas = this.$refs.dummy;
        let context = canvas.getContext("2d");
        this.image.width = canvas.width;
        this.image.height = canvas.height;
        context.drawImage(this.image, 0, 0, canvas.width, canvas.height);
        imageData = context.getImageData(0, 0, canvas.width, canvas.height);
        context.clearRect(0, 0, canvas.width, canvas.height);
        this.worker.postMessage(
          {
            isInitial: true,
            canvas: this.canvas,
            image: imageData,
          },
          [this.canvas]
        );
      };
    },
    debounce(func, delay){ 
    let debounceTimer 
    return function() { 
        const context = this
        const args = arguments 
            clearTimeout(debounceTimer) 
                debounceTimer 
            = setTimeout(() => func.apply(context, args), delay) 
    } 
}  
  },
  destoryed(){
    this.worker.terminate();
  }
};
</script>
<style>
.dummy {
  display: none;
}
